'use strict'
import React from 'react';
import ScreenComponent from './../../../components/ScreenComponent';

export default class CoopMorePopup extends ScreenComponent {
    constructor(...props){
        super(...props);
    }
    _stopPropagation = (e) => {
        e.stopPropagation();
    }
    _onClose = () => {
        this.props.onClose&&this.props.onClose();
    }
    render(){
        return <div className="use-note-popup-content" onClick={this._stopPropagation}>
            <div className="popup-content">
                <p></p>
                <p className="about-trj">
                    <span></span>
                </p>
                <p className="about-trj about-lx-trj">
                    <span></span>
                </p>
            </div>
            <p className="close">
                <a className="icon-white-close"
                    onClick={this._onClose}
                    ></a>
            </p>
        </div>
    }
}



