//
function trim(str) {
  return str.replace(/(^\s*)|(\s*$)/g, "");
}

/**
 * @desc 15
 * @param {String} idCard15 15
 * @return {Boolean}
 */

function isValidityBrithBy15IdCard(idCard15) {
  let year = idCard15.substring(6, 8);
  let month = idCard15.substring(8, 10);
  let day = idCard15.substring(10, 12);
  let temp_date = new Date(year, parseFloat(month) - 1, parseFloat(day));
  // getYear()
  if (
    temp_date.getYear() != parseFloat(year) ||
    temp_date.getMonth() != parseFloat(month) - 1 ||
    temp_date.getDate() != parseFloat(day)
  ) {
    return false;
  } else {
    return true;
  }
}

/**
 * @desc 18
 * @param {String} idCard 18
 * @return {Boolean}
 */

function isValidityBrithBy18IdCard(idCard18) {
  let year = idCard18.substring(6, 10);
  let month = idCard18.substring(10, 12);
  let day = idCard18.substring(12, 14);
  let temp_date = new Date(year, parseFloat(month) - 1, parseFloat(day));
  // getFullYear()
  if (
    temp_date.getFullYear() != parseFloat(year) ||
    temp_date.getMonth() != parseFloat(month) - 1 ||
    temp_date.getDate() != parseFloat(day)
  ) {
    return false;
  } else {
    return true;
  }
}

/**
 * @desc 18
 * @param {Array} a_idCard 
 * @return {Boolean}
 */

function isTrueValidateCodeBy18IdCard(a_idCard) {
  let sum = 0; // 
  let ValideCode = [1, 0, 10, 9, 8, 7, 6, 5, 4, 3, 2]; // .10X
  let Wi = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2, 1]; // 
  if (a_idCard[17].toLowerCase() == "x") {
    a_idCard[17] = 10; // x10
  }
  for (let i = 0; i < 17; i++) {
    sum += Wi[i] * a_idCard[i]; // 
  }
  let valCodePosition = sum % 11; // 
  if (a_idCard[17] == ValideCode[valCodePosition]) {
    return true;
  } else {
    return false;
  }
}
/**
 *
 * @desc 
 * @param {String} idCard
 * @returns {Boolean}
 */
function IdCardValidate(idCard) {
  idCard = trim(idCard.replace(/ /g, "")); //
  if (idCard.length === 15) {
    return isValidityBrithBy15IdCard(idCard); //15
  } else if (idCard.length === 18) {
    let a_idCard = idCard.split(""); // 
    if (
      isValidityBrithBy18IdCard(idCard) &&
      isTrueValidateCodeBy18IdCard(a_idCard)
    ) {
      //1818
      return true;
    } else {
      return false;
    }
  } else {
    return false;
  }
}

export default {
  IdCardValidate
}


