'use strict'
import React from 'react';
import ScreenComponent from './../../../components/ScreenComponent';
import api from './../../../../controllers/api';

export default class ProtocolPopup extends ScreenComponent {
    constructor(...props){
        super(...props);
        this.state = {
            lxProtocolData: {}
        }
    }
    componentDidMount() {
        api.lxProtocol({type: 0}).success((res) => {
            this.setState({
                lxProtocolData: res.data || {}
            })
        }).error((data) => {
            this.getScreen().toast(data.message)
        })
    }
    _stopPropagation = (e) => {
        e.stopPropagation();
    }
    _onClose = () => {
        this.props.onClose&&this.props.onClose();
    }
    render(){
        const { content } = this.state.lxProtocolData;
        return <div className="protocol-popup-content" onClick={this._stopPropagation}>
            <div className="popup-content">
                <div className="close" onClick={this._onClose}><i className="icon-del-big"></i></div>
                <p className="title"></p>
                <div className="protocol">
                    <div className="text">
                        <div className="protocol-content">
                            <p style={{color: '#333'}} dangerouslySetInnerHTML={{__html: content}}></p>
                        {
                            // <p></p>
                            // <p> </p>
                            // <p>()</p>
                            // <h4></h4>
                            // <p>()</p>
                            // <h4></h4>
                            // <p>1.1</p>
                            // <p>1.2</p>
                            // <p>1.3</p>
                            // <p>1.4</p>
                            // <p>1.5</p>
                            // <p>1.6</p>
                            // <p>1.7</p>
                            // <p>1.8</p>
                            // <p>1.9</p>
                            // <p>1.10</p>
                            // <p>1.11</p>
                            // <p>1.12</p>
                            // <h4></h4>
                            // <p>2.1</p>
                            // <p>2.2</p>
                            // <p>2.3</p>
                            // <p>2.4</p>
                            // <p>2.5</p>
                            // <p>2.6</p>
                            // <h4></h4>
                            // <p>3.1 3.2</p>
                            // <h4></h4>
                            // <p></p>
                            // <p></p>
                            // <h4></h4>
                            // <p></p>
                            // <h4></h4>
                            // <p>6.1</p>
                            // <p>6.2 </p>
                            // <p>6.3</p>
                        }
                        </div>
                    </div>
                </div>
            </div>
        </div>
    }
}



