import React from 'react';

function Authorize(props) {
    return (
        <div
            style={{
                height:'100%',
                width:'100%',
                backgroundImage:'url(images/index/bg.png)',
                backgroundSize:'100% 100%',
                position:'relative'
            }}>
            <div
                style={{
                    height:'100%',
                    width:'100%',
                    position:'absolute',
                    backgroundColor:'rgba(120,120,120,0.5)'
                }}>

            </div>
            <div className="authorized">
                <a 
                    onClick={props.onClose}
                    className="closed"></a>
                <div className="accredit_tit"></div>
                <div className="accredit_ts">, <em></em> </div>
                <div className="accredit_hq"><i></i>()</div>
                <div className="accredit_ty"><em onClick={props.onProtocol}></em></div>
                <a 
                    href="javascript:" 
                    onClick={props.onConfirm}
                    className="accredit_btn"></a>
            </div>
        </div>
    )
}

export default Authorize;


