'use strict'
import React, { Component } from 'react';
import ScreenComponent from './../../../components/ScreenComponent';

import api from './../../../../controllers/api';

class PaymentSuccess extends ScreenComponent {
    static pageConfig = {
        path: '/member/payment/success',
        permission: true
    }
    constructor(...props) {
        super(...props);
        this.navigationOptions = {
            title: ''
        }
        this.state = {
            detail: {}
        }
    }

    componentDidMount() {
        const {
            params
        } = this.getScreen().getNavigation().state;
        this._getDetail(params.order_id);
    }

    _getDetail(order_id) {
        api.orderInfo({
            order_id: order_id
        }).success((content) => {
            console.log('onsuccess>>>>', content)
            this.setState({
                detail: content.data || {}
            })
        }).error((data) => {
            this.getScreen().toast(data.message)
        })
    }

    render() {
        let detail = this.state.detail;
        return (
            <div className='payment-success'>
                <dl className='-success-head'>
                    <dd><i className='icon-success'></i></dd>
                    <dt></dt>
                </dl>
                <div className='-success-body'>
                    <h3>{detail.merchant_name}</h3>
                    <ul>
                        <li><label></label><span>{detail.money}</span></li>
                        {/*<li><label></label><span>-{detail.money - detail.pay_money}</span></li>*/}
                        <li><label></label><span>-{detail.discount_amount}</span></li>
                        <li><label></label><span className='-orange'>{detail.pay_money}</span></li>
                        <li><label></label><span>{detail.order_no}</span></li>
                        <li><label></label><span>{detail.mtime}</span></li>
                        <li><label></label><span>{detail.mobile}</span></li>
                    </ul>
                </div>
            </div>
        )
    }
}

export default PaymentSuccess;   


