'use strict'
import React, { Component } from 'react';
import ScreenComponent from './../../../components/ScreenComponent';

import api from './../../../../controllers/api';

export default class CouponDetail extends ScreenComponent {
    static pageConfig = {
        path: '/member/coupon/detail',
        permission: true
    }

    constructor(...props) {
        super(...props);
        this.navigationOptions = {
            title: ''
        }
        this.state = {
            detail: {},// status 1-2-3- ; 4-5-6-
            refresh: false
        }
    }

    componentDidMount() {
        this._getDetail();
    }
    
    _getDetail() {
        let navigation = this.getScreen().getNavigation();
        let { params } = navigation.state;
        api.viewCoupon({
            id: params.coupon_id
        }).success((res) => {
            console.log('onsuccess>>>>', res)
            this.setState({
                detail: res.data || {}
            })
        }).error((res) => {
            this.getScreen().toast(res.message)
        })
    }

    _confirmUnpublish(id) {
        this.getScreen().alert({
            message: '',
            buttons: [
                {
                    text: ''
                },
                {
                    text: "",
                    onPress: () => {
                        this._unPublish(id);
                    }
                }
            ]
        });
    }

    contribute() {
        return {
            refresh: this.state.refresh
        };
    }

    _unPublish(id){
        api.shelvesMerchant({
            coupon_id: id
        }).success((res) => {
            this.state.refresh = true
            this.getScreen().toast('', 2800, () => {
                this.getScreen().getNavigation().goBack();
            })
        }).error((res) => {
            this.getScreen().toast(res.message)
        })
    }

    _CouponProps(detail){
        return(
            <div className='cell -right-line'
                style={detail.status == 6 ? {marginBottom: 0} : {}}
            >
                <div className='cell-item'>
                    <div className='-label'></div>
                    <div className='-value'>{detail.type_name}</div>
                </div>
                {
                    detail.type != 1 &&
                    <div className='cell-item'>
                        <div className='-label'>-</div>
                        <div className='-value'>{detail.need_amount}</div>
                    </div>
                }
                <div className='cell-item'>
                    <div className='-label'>-</div>
                    <div className='-value'>{detail.discount_amount}</div>
                </div>
                <div className='cell-item'>
                    <div className='-label'></div>
                    <div className='-value'>{detail.publish_num}</div>
                </div>
                <div className='cell-item'>
                    <div className='-label'></div>
                    <div className='-value'>{detail.scope_name}</div>
                </div>
                <div className='cell-item'>
                    <div className='-label'></div>
                    <div className='-value'>{detail.week_need}</div>
                </div>
                <div className='cell-item'>
                    <div className='-label'></div>
                    <div className='-value'>{detail.use_time_end}</div>
                </div>
                <div className='cell-item'>
                    <div className='-label'></div>
                    <div className='-value'>{detail.show_time_end}</div>
                </div>
                <div className='cell-item'>
                    <div className='-label'></div>
                    <div className='-value'>{detail.is_free == '0' ? '' : ''}</div>
                </div>
                <div className='cell-item'>
                    <div className='-label'></div>
                    <div className='-value'>{detail.store_name}</div>
                </div>
                <div className='cell-item'>
                    <div className='-label'></div>
                    <div className='-value'>{detail.use_rule}</div>
                </div>
                {
                    detail.status == 5 &&
                    <div className='cell-control'>
                        <button onClick={this._confirmUnpublish.bind(this,detail.id)} className='btn-primary'></button>
                    </div>
                }
            </div>
        )
    }
    render() {
        let detail = this.state.detail || {}
        return (
            <div className='coupon-detail'>
                {
                    !_.isEmpty(detail.reason) &&
                    <div className='coupon-msg -orange -tal'>{detail.reason}</div>
                }
                {
                    detail.status == 5 &&
                    <div className='coupon-msg'></div>
                }
                {
                    detail.status == 6 &&
                    <div className='coupon-msg -orange'></div>
                }
                {this._CouponProps(detail)}
            </div>
        )
    }
}



