'use strict'
import React, { Component } from 'react';
import ScreenComponent from './../../../components/ScreenComponent';

import api from './../../../../controllers/api';

class SubMerchantIndex extends ScreenComponent {
    static pageConfig = {
        path: '/submerchant/index',
        permission: true
    }
    constructor(...props) {
        super(...props);
        this.navigationOptions = {
            title: ''
        }
        this.state = {
            data: {}
        }
    }

    componentDidMount() {
        this._getData();
    }

    _getData() {
        api.indexPage({

        }).success((content) => {
            console.log('onsuccess>>>>', content)
            this.setState({
                data: content.data || {}
            })
        }).error((data) => {
            this.getScreen().toast(data.message)
        })
    }

    _MerchantName(data) {
        return (
            <div className='-merchant-name'>
                <div className='picture'><img src={data.logo} /></div>
                <div className="account-info">
                    <p className="account-name"></p>
                    <p className="account-info-cont"><em></em>  <em>13234699900</em>  <em></em> </p>
                    <p className="account-orange"></p>
                </div>
                <i className="icon-arrow"></i>
            </div>
        )
    }
    _MerchantBalance(data) {
        return (
            <div className='-merchant-balance'>
                <dl className='-balance-module'>
                    <dt>()</dt>
                    <dd>{data.money}</dd>
                </dl>
                <div className='-handle'><a href='javascript:;'></a><a href='javascript:;'></a></div>
            </div>
        )
    }
    _MerchantMenu(data) {
        return (
            <div className='-merchant-menu'>
                <dl>
                    <dd></dd>
                    <dt></dt>
                </dl>
                <dl>
                    <dd></dd>
                    <dt></dt>
                </dl>
                <dl>
                    <dd></dd>
                    <dt></dt>
                </dl>
                <dl>
                    <dd></dd>
                    <dt></dt>
                </dl>
            </div>
        )
    }
    _MerchantAward(data) {
        return (
            <div className='-merchant-award'>
                <div className='sub-merchant-head'>
                    <div className="sub-div">
                        <p className="head-tit">()</p>
                        <p className="head-money">{data.prize_money}</p>
                    </div>
                    <a href=''></a>
                </div>
                <div className='body'></div>
            </div>
        )
    }
    render() {
        let data = this.state.data || {};
        return (
            <div className='my-merchant'>
                {this._MerchantName(data)}
                {this._MerchantBalance(data)}
                {this._MerchantMenu(data)}
                {this._MerchantAward(data)}
                <div className='-merchant-invite'></div>
            </div>
        )
    }
}

export default SubMerchantIndex;   


