import React, { Component } from 'react';
import ScreenComponent from './../../../components/ScreenComponent';

class BuyBar extends ScreenComponent {
    constructor(...props){
        super(...props);
    }
    _buy=()=>{
        let {
            user_point,
            need_amount,
            coupon_point,
            coupon_id,
            make_times,
            mid
        } = this.props;
        if(make_times<1){
            this.getScreen().alert({
                title:'',
                message:'',
                buttons:[
                    {text:''},
                    //{text:'',onPress:this._getPoint},
                ]
            })
        }else{
            this.getScreen().getNavigation().navigate('Exchange',{
                id:coupon_id,
                source_type:'index'
            });
        }
    }  
    _yaoyiyao(type){
        let {
            user_point,
            need_amount,
            coupon_point,
            coupon_id,
            make_times,
            mid,
            id,
            extra_money
        } = this.props;
        if(type==='jp'){
            if(make_times<1){
                this.getScreen().alert({
                    message:<span><br/>{extra_money}</span>,
                    buttons:[
                        {text:''},
                        //{text:'',onPress:this._getPoint},
                    ]
                })
            }else{
                this.getScreen().getNavigation().navigate('Shake',{
                    id:coupon_id,
                    type,
                    mid,
                    shake_id:id,
                    source_type:'index',
                });
            }
        }else if(type==='jf'){
            if(make_times<1){
                this.getScreen().alert({
                    message:<span><br/></span>,
                    buttons:[
                        {text:''},
                        //{text:'',onPress:this._getPoint},
                    ]
                })
            }else{
                this.getScreen().getNavigation().navigate('Shake',{
                    id:coupon_id,
                    type,
                    mid,
                    source_type:'index',
                });
            }
        }   
        
    } 
    _getPoint(){

    }
    render() {
        let {
            user_point,
            need_point,
            is_free,
            fromIndex,
            coupon_id,
            id,
            mid
        } = this.props
        if (!fromIndex) {
            return null;
        }
        if (is_free=='1') {
            return (
                <div className='s-luck-draw'>
                    <p>
                        <a
                            href='javascript:void(0)'
                            onClick={()=>this._yaoyiyao('jp')}
                            className="draw_btn">
                            </a>
                    </p>
                </div>
            );
        } else {
            return (
                <div className='s-luck-draw'>
                    <p className="points"> : {user_point}</p>
                    <p>
                        <a 
                            href='javascript:void(0)'
                            onClick={()=>this._yaoyiyao('jf')}
                            className="draw_btn_l">
                            {need_point}
                        </a>
                        <a  
                            onClick = {this._buy}
                            href='javascript:void(0)'
                            className="draw_btn_r">
                            
                        </a>
                    </p>
                </div>
            )
        }

    }
}
export default BuyBar;


